export default {
  namespaced: true,
  state: {
    result: {
      srvMstNm: '',
      srvMstTg: '',
      pbshDt: '',
      exprDt: '',
      srvMstItems: []
    }
  },
  getters: {
    // getItem: (state) => (index) => state.list[index].model
  },
  mutations: {
    setItem (state, param) {
      state.result.srvMstNm = param.srvMstNm
      state.result.srvMstTg = param.srvMstTg
      state.result.pbshDt = ''
      state.result.exprDt = ''
      state.result.srvMstItems = ''
    }
  },
  actions: {
    /*
    addItem: ({commit}, obj) => commit('_add', obj)
    */
  }
}



