import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: () => import('@/components/survey/main/container/IndexContainer')
    },
    {
      path: '/workspace/list',
      name: 'list',
      meta: {mode: 'list', title: '설문지목록'},
      component: () => import('@/components/survey/list/container/SurveyListContainer')
    },
    {
      path: '/workspace/register',
      name: 'register',
      meta: {mode: 'register', title: '설문지등록'},
      component: () => import('@/components/survey/management/container/WorkSpaceContainer')
    },
    {
      path: '/workspace/modify/:srvMstrId',
      name: 'modify',
      meta: {mode: 'modify', title: '설문지수정'},
      component: () => import('@/components/survey/management/container/WorkSpaceContainer')
    },
    {
      path: '/workspace/detail/:srvMstrId',
      name: 'detail',
      meta: {mode: 'detail', title: '설문지상세'},
      component: () => import('@/components/survey/management/container/WorkSpaceContainer')
    },
    {
      path: '/workspace/response/:srvMstrId',
      name: 'response',
      meta: {title: '설문지응답'},
      component: () => import('@/components/survey/response/container/ResponseSpaceContainer')
    },
    {
      path: '/workspace/result/:srvMstrId',
      name: 'result',
      meta: {title: '설문결과'},
      component: () => import('@/components/survey/result/container/ResultContainer')
    }
  ]
})



